<template>
    <div style="border-radius: 5px;">
        <el-table height="460" style="border-radius: 5px;" :data="userdata.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))" empty-text="获取用户数据失败或数据为空">
            <el-table-column label="ID" prop="id" align="center">
            </el-table-column>
            <el-table-column label="用户名" prop="username" align="center" sortable>
            </el-table-column>
            <el-table-column label="密码" align="center" prop="passwords" :formatter="isShowPass">

            </el-table-column>
            <el-table-column align="right">
                <template slot="header" slot-scope="scope">
                    <el-input size="mini" aria-placeholder="请输入要查找的用户名" v-model="search" suffix-icon="el-icon-search"></el-input>
                </template>
                <template slot-scope="scope">
                    <el-button type="warning" icon="el-icon-view" @click="changeState(scope.row)"></el-button>
                    <el-button icon="el-icon-edit" type="primary"></el-button>
                    <el-button icon="el-icon-delete" type="danger" @click="deleteUser(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "usermanage",
        data(){
            return{
                userdata:[],
                search:""
            }
        },
        methods:{
            isShowPass(row,column){
                if(!row.state)
                    return "**********";
                else
                    return row.passwords;
            },
            changeState(row){
                row.state=!row.state;
            },
            deleteUser(row){
                this.userdata.forEach((item,index)=>{
                   if(row.id==item.id){
                       this.userdata.splice(index,1);
                   }
                });
            }
        },
        created(){
            this.userdata=JSON.parse(sessionStorage.getItem("users"));
        }
    }
</script>

<style scoped>
</style>



