<template>
    <div style="border-radius: 5px;">
        <el-table height="460" style="border-radius: 5px;" :data="orderdata.filter(data => !search || data.id.toLowerCase().includes(search.toLowerCase()))" empty-text="获取用户数据失败或数据为空">
            <el-table-column label="订单号" prop="id" width="200">
            </el-table-column>
            <el-table-column label="用户名" prop="username"  sortable  width="150">
            </el-table-column>
            <el-table-column label="订单生成时间"  prop="ortime" width="300">
            </el-table-column>
            <el-table-column label="订单状态"  prop="state" :formatter="isShowPass"  width="150" align="center">
            </el-table-column>
            <el-table-column width="300" align="right">
                <template slot="header" slot-scope="scope">
                    <el-input size="mini" aria-placeholder="请输入要查找的订单号" v-model="search" suffix-icon="el-icon-search"></el-input>
                </template>
                <template slot-scope="scope">
                    <el-button size="mini" type="warning" icon="el-icon-view" @click="test(scope.row)">查看</el-button>
                    <el-button size="mini" icon="el-icon-check" type="primary" @click="scope.row.state=1" :disabled="scope.row.state!=0">确认</el-button>
                    <el-button size="mini" icon="el-icon-delete" type="danger" @click="scope.row.state=-1" :disabled="scope.row.state!=0">取消</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "ordermanage",
        data(){
            return{
                orderdata:[],
                search:""
            }
        },
        methods:{
            isShowPass(row,column){
                if(row.state==0)
                    return "待审核";
                else if(row.state==-1)
                    return "订单有误，不通过";
                else
                    return "订单已完成";
            },
            changeState(row,val){
                row.state=val;
            },
            test(row){
                console.log(row)
            }
        },
        created(){
            this.orderdata=JSON.parse(sessionStorage.getItem("orders"));
        }
    }
</script>

<style scoped>
</style>



