<template>
    <div>
        <el-container class="main-body">
            <el-header>
                <el-row>
                    <el-col :span="4">
                        <div class="header-label">后台管理</div>
                    </el-col>
                    <el-col :span="4" :push="15">
                        <el-link class="sub-header" href="/index">退出</el-link>
                    </el-col>
                </el-row>
            </el-header>
            <el-container class="sub-body">
                <el-aside>
                    <el-menu default-active="1" @open="manage" @close="manage">
                        <el-submenu v-for="(kind,index) in kinds" :index="index.toString()" :key="index">
                            <template slot="title">
                                <i class="el-icon-goods"></i>
                                <span>{{kind}}</span>
                            </template>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <!--                    <UserManage :users="users"></UserManage>-->
                    <component :is="select">

                    </component>
                </el-main>
            </el-container>
            <el-footer>
                <div style="color: #eeeeee;line-height: 400%">&copy;powered by asxlwsl</div>
            </el-footer>
        </el-container>
    </div>
</template>

<script>
    import usermanage from "@/components/usermanage";
    import ordermanage from "@/components/ordermanage";

    export default {
        name: "Back",
        data() {
            return {
                kinds: ["用户管理", "订单管理", "商品管理"],
                com: ['usermanage', 'oredermanage'],
                select: "usermanage"
            }
        },
        components: {
            'usermanage': usermanage,
            'oredermanage': ordermanage
        },
        methods: {
            manage(index) {
                this.select = this.com[index];
            }
        },
        created() {
            let u = [
                {id: 1, username: "test1", passwords: "test1", state: false},
                {id: 2, username: "test1", passwords: "test1", state: false},
                {id: 3, username: "test1", passwords: "test1", state: false},
                {id: 4, username: "test1", passwords: "test1", state: false},
                {id: 5, username: "test1", passwords: "test1", state: false},
                {id: 6, username: "test1", passwords: "test1", state: false},
                {id: 7, username: "test1", passwords: "test1", state: false},
                {id: 8, username: "test1", passwords: "test1", state: false}
            ];
            // u.forEach((row) => {
            //     row=JSON.stringify(row);
            // });
            u=JSON.stringify(u);
            sessionStorage.setItem("users", u);

            let or = [
                {id: "1", username: "test1", ortime: Date(), state: false},
                {id: "2", username: "test1", ortime: Date(), state: false},
                {id: "3", username: "test1", ortime: Date(), state: false},
                {id: "4", username: "test1", ortime: Date(), state: false},
                {id: "5", username: "test1", ortime: Date(), state: false},
                {id: "6", username: "test1", ortime: Date(), state: false},
                {id: "7", username: "test1", ortime: Date(), state: false},
                {id: "8", username: "test1", ortime: Date(), state: false}
            ];
            or=JSON.stringify(or);
            sessionStorage.setItem("orders", or);
        }
    }
</script>

<style scoped>
    .main-body {
        background-color: #CCCCCC;
        background: rgba(0, 0, 0, 0.5);
        height: 600px;
        border-radius: 10px;
    }

    .el-header {
        background-color: cornflowerblue;
        border-radius: 10px 10px 0 0;
    }

    .header-label {
        height: 60px;
        text-align: center;
        background-color: transparent;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        font-size: 24pt;
        line-height: 150%;
    }

    .el-aside {
        height: 520px;
        background: rgba(112, 122, 125, 0.8);
    }

    .sub-body {
        overflow: hidden;
    }

    .el-footer {
        background: rgba(111, 111, 111, 0.6);
        border-radius: 0 0 10px 10px;
    }

    .sub-header {
        line-height: 60px;
    }

    .el-main {
        background: rgba(110, 110, 110, 0.8);
        height: 520px;
        overflow: hidden;
    }
</style>



