<template>
        <el-container style="width: 500px;">
            <el-header style="background-color: cornflowerblue">
                <h1 v-text="title"></h1><br>
            </el-header>
            <el-main>
                <el-radio-group v-model="title" @change="clear">
                    <el-radio-button :label="selects[0]">{{selects[0]}}</el-radio-button>
                    <el-radio-button :label="selects[1]">{{selects[1]}}</el-radio-button>
                </el-radio-group>
                <hr>
                <div style="display: flex">
                    <label style="width: 100px;line-height: 250%" class="el-icon-user-solid">用户名</label>
                    <el-input v-model="inputs[0]"></el-input>
                </div>
                <br>
                <div style="display: flex">
                    <label style="width: 100px;line-height: 250%" class="el-icon-lock">密码</label>
                    <el-input v-model="inputs[1]" type="password"></el-input>
                </div>
                <br>
                <div style="display: flex" v-if="title=='登录'">
                    <label style="width: 82px;line-height: 250%">验证码</label>
                    <el-input v-model="inputs[2]" style="width: 200px; "></el-input>
                    <el-image style="margin-left: 50px"></el-image><span>1234</span>
                </div>
                <div style="display: flex" v-else-if="title=='注册'">
                    <label style="width: 100px;line-height: 250%" class="el-icon-lock">确认密码</label>
                    <el-input v-model="inputs[2]" type="password"></el-input>
                </div>
                <br>
                <el-button v-html="title" primary @click="onit"></el-button>
            </el-main>
        </el-container>
</template>

<script>
    export default {
        name: "Index",
        data(){
            return{
                title:"登录",
                selects:["登录","注册"],
                logininfo:{name:'',password:'',checkcode:''},
                registerinfo:{name:'',password:'',repass:''},
                inputs:Array(3),
                labels:[]
            }
        },
        methods:{
            login:function(){
                this.add(true);
                sessionStorage.setItem("user",this.logininfo.name.trim());
                this.$router.replace("/");
            },
            register:function(){
                this.add(false);
            },
            onit:function () {
                if(this.title=="登录")
                    this.login();
                else
                    this.register();
            },
            add:function(fa){
                var i=0;
                if(fa){
                    for (var Key in this.logininfo) {
                        this.logininfo[Key]=this.inputs[i++];
                    }
                }
                else{
                    for (var Key in this.registerinfo) {
                        this.registerinfo[Key]=this.inputs[i++];
                    }
                }
            },
            clear:function () {
                this.inputs=new Array(3);
            }

        }
    }
</script>

<style scoped>
    .el-container{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        background-color: antiquewhite;
        border-radius: 10px;
    }
    .el-header{
        border-radius: 10px 10px 0 0;
    }
    .el-main{
        border-radius: 0 0 10px 10px;
        background-color: #eeeeee;
        height: 320px;
    }
    h1{
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        font-weight: 700;
    }
</style>



