<template>
    <div>
        <el-container class="main-body">
            <el-header>
                <el-row>
                    <el-col :span="4"><div class="header-label">asxlwsl</div></el-col>
                    <el-col :offset="6" :span="5"><div class="header-label">图书销售系统</div></el-col>
                    <el-col :span="1" :push="2" class="header-label" v-if="!user"><el-link href="/index">登录</el-link></el-col>
                    <el-col :span="1" :push="2" class="header-label" v-if="!user"><el-link href="/index">注册</el-link></el-col>
                    <el-col :span="1" :push="2" class="header-label" v-if="user">
                        <el-dropdown>
                            <el-avatar :size="30" style="margin-top: 15px" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"></el-avatar>
<!--                            <i class="el-icon-user-solid"></i>-->
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item>
                                    <el-link href="/userzone">个人中心</el-link>
                                </el-dropdown-item>
                                <el-dropdown-item>我的购物车</el-dropdown-item>
                                <el-dropdown-item><span @click="logout">退出登录</span></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-col>
                    <el-col :span="4" :push="2" style="top: 10px">
                        <el-input>
                            <div slot="append"><el-button icon="el-icon-search"></el-button></div>
                        </el-input>
                    </el-col>
                </el-row>
            </el-header>
            <el-container>
                <el-aside>
                    <el-menu default-active="0" active-text-color="#FF9E00">
                        <el-menu-item v-for="(item,index) in bookkinds" class="asides" :index="index.toString()" :key="index">
                            <i class="el-icon-reading"></i>
                            <span slot="title">{{item}}</span>
                        </el-menu-item>
                    </el-menu>
                </el-aside>
                <el-main>
                    <Book :books="booksinfo" @additems="additem"></Book>
                </el-main>
            </el-container>
            <el-footer>
                <div style="color: #eeeeee;line-height: 400%">&copy;powered by asxlwsl</div>
            </el-footer>
        </el-container>
    </div>
</template>

<script>
    import Book from '@/components/book.vue';
    export default {
        name: "Home",
        data(){
            return{
                bookkinds:["推荐","文学","励志","科学","玄幻"],
                user:sessionStorage.getItem("user"),
                booksinfo:[],
                selectlists:[]
            }
        },
        created(){
            var books=[
                {id:"1",name:"test1",price:"0.00"},
                {id:"2",name:"test2",price:"0.00"},
                {id:"3",name:"test2",price:"0.00"},
                {id:"4",name:"test2",price:"0.00"},
                {id:"5",name:"test2",price:"0.00"},
                {id:"6",name:"test2",price:"0.00"},
                {id:"7",name:"test2",price:"0.00"},
                {id:"8",name:"test2",price:"0.00"},
            ];
            books.forEach((row)=>{
                row.state=false;
                row.text="加入购物车";
            })
            this.booksinfo=books;
        },
        methods:{
            tologin:function () {
                console.log(this.bookinfos);
                sessionStorage.setItem("user","");
                sessionStorage.setItem("state","true");
            },
            logout:function () {
                this.user=null;
                sessionStorage.setItem("user","");
            },
            additem(index){
                console.log(index);
                this.booksinfo.forEach((row)=>{
                    if(row.id==index){
                        row.state=true;
                        row.text="已加入";
                        this.selectlists.push(row);
                    }

                })
            }
        },
        components:{
            contents:Book
        }
    }
</script>

<style scoped>
    .main-body{
        background-color: #CCCCCC;
        background: rgba(0,0,0,0.5);
        height: 600px;
        border-radius: 10px;
    }
    .el-header{
        background-color: cornflowerblue;
        border-radius: 10px 10px 0 0;
    }
    .header-label{
        text-align: center;
        background-color: transparent;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        line-height: 200%;
        font-size: 20pt;
    }
    .el-aside{
        height: 520px;
        background: rgba(112,122,125,0.8);
    }
    .el-footer{
        background: rgba(111,111,111,1);
        border-radius: 0 0 10px 10px;
    }
    .el-main{
        height: 520px;
        background: rgba(110,110,110,0.8);
        overflow: hidden;
    }
    .el-menu > .el-menu-item.is-active{
        border-bottom: 5px solid orange;
    }
</style>



