<template>
    <div style="border-radius: 5px;">
        <el-table show-summary height="500" style="border-radius: 5px;" :data="bookinfos.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))">
            <el-table-column label="ID" prop="id" align="center">
            </el-table-column>
            <el-table-column label="书名" prop="name" align="center">
            </el-table-column>
            <el-table-column label="单价" prop="price" align="center">
            </el-table-column>
            <el-table-column align="right">
                <template slot="header" slot-scope="scope">
                    <el-input size="mini" aria-placeholder="请输入要查找的书名" v-model="search" suffix-icon="el-icon-search"></el-input>
                </template>
                <template slot-scope="scope">
                    <el-button icon="el-icon-shopping-bag-1" @click="putIn(scope.row)" type="primary"  :disabled="scope.row.state">{{scope.row.text}}</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "book",
        props:["books"],
        data(){
            return{
                bookinfos:this.books,
                search:"",
            }
        },
        methods:{
            putIn(row) {
               var load=this.$loading({
                  lock:true,
                  text:"loading,please wait...",
                  spinner:"el-icon-loading",
                  background:"rgba(50,50,50,0.6)",
               });
               setTimeout(()=>{
                   this.$emit("additems",row.id);
                   load.close();
                   this.$message(
                       {
                           message:"加入成功！",
                           center:true,
                           type:"success"
                       });
               },2000);

            }
        }
    }
</script>

<style scoped>

</style>



