<template>
    <div>
        <el-container class="main-body">
            <el-header>
                <el-row>
                    <el-col :span="4"><div class="header-label">asxlwsl</div></el-col>
                    <el-col :offset="6" :span="5"><div class="header-label">图书销售系统</div></el-col>
                    <el-col :span="1" :push="2" class="header-label"><el-link href="/">返回首页</el-link></el-col>
                </el-row>
            </el-header>
            <el-container>
                <el-aside>
                    <el-radio-group >
                        <el-radio-button style="width: 500px">111</el-radio-button>
                    </el-radio-group>
                </el-aside>
                <el-main>

                </el-main>
            </el-container>
            <el-footer>
                <div style="color: #eeeeee;line-height: 400%">&copy;powered by asxlwsl</div>
            </el-footer>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "UserPage",
        data(){
            return{
                user:""
            }
        },
        created(){
            var usr=sessionStorage.getItem("user");
            console.log(usr);
            this.user=usr;
            if(!usr || usr==""){
                alert("当前未登录，无权访问!");
                this.$router.push({path:'/'})
            }
        }
    }
</script>

<style scoped>
    .main-body{
        background-color: #CCCCCC;
        background: rgba(0,0,0,0.5);
        height: 600px;
        border-radius: 10px;
    }
    .el-header{
        background-color: cornflowerblue;
        border-radius: 10px 10px 0 0;
    }
    .header-label{
        text-align: center;
        background-color: transparent;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        line-height: 200%;
        font-size: 20pt;
    }
    .el-aside{
        height: 520px;
        background: rgba(112,122,125,0.8);
    }
    .el-footer{
        background: rgba(111,111,111,1);
        border-radius: 0 0 10px 10px;
    }
    .el-main{
        height: 520px;
        background: rgba(110,110,110,0.8);
        overflow: hidden;
    }
</style>



